.row{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: start;
	    align-items: flex-start;
}

.row .column{
	width: 50%;
	text-align: center;
}

.column p{
	text-align: left;
}

.column img{
	width: 100%;
	max-width:309px;
}

@media screen and (max-width: 767px) {
	.row{
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.row .column{
		width: 100%!important;
		margin-bottom: 10px;
	}
}


